<template>
    <div>
        <u-container width="800px" height="300px">
            <u-svga slot="background" width="650px" height="250px" @on-loaded="loaded = true"
                :paused="paused" :src="svgaurl" :setTextList="textList" :setImageList="imageList" ></u-svga>
            <u-bitmap v-if="!loaded" slot="foreground" width="200px" height="200px" :skin="webpImg">{{loadingText}}</u-bitmap>
        </u-container>

        <u-button v-if="!loaded" type="default" @on-click="onBtnPlay">开始加载</u-button>
        <u-button type="default" @on-click="onBtnClick">{{(paused && '播放') || '暂停'}}</u-button>
        <br>
        <u-svga width="200px" height="300px" :src="richSrc"></u-svga>

        <br>
        <u-button type="default" @on-click="onBtnChanged">更换</u-button>

        <br>
        <u-svga width="400px" height="300px" :src="publishSrc"></u-svga>
    </div>
</template>
<script>
    import svgaSrc from './img/svga/blinddate_bao_deng.svga';
    import publishSrc from './img/svga/blinddate_publish.svga';
    import svgaSrc1 from './img/svga/rich_bg_lv1.svga';
    import svgaSrc2 from './img/svga/rich_bg_lv2.svga';
    import svgaSrc3 from './img/svga/rich_bg_lv3.svga';
    import svgaSrc4 from './img/svga/rich_bg_lv4.svga';
    import svgaBCSrc from './img/svga/bc_effect_2.svga';

    import faverBtn from './img/bg_gift_hover.png';
    import webpImg from './img/loading.webp';

    export default {
        data () {
            return {
                svgaSrc,
                publishSrc,
                svgaBCSrc,
                richSrcs: [svgaSrc1, svgaSrc2, svgaSrc3, svgaSrc4],
                richSrc: svgaSrc1,
                loadingText: '点击"开始加载"按钮加载资源',
                webpImg,
                loaded: false,
                svgaurl: null,
                richLevel: 0,
                paused: false,
                textList: [],
                imageList: []
            };
        },
        methods: {
            onBtnClick() {
                this.paused = !this.paused;
            },
            onBtnChanged() {
                this.richSrc = this.richSrcs[++this.richLevel % 4];
            },
            onBtnPlay() {
                this.svgaurl = this.svgaBCSrc;
                this.textList = [
                    {forKey:'from_nick',
                        value:{
                            text: '张三',
                            size: '12px',
                            color: '#00ff00',
                            family: '宋体',
                            offset: {x: 0.0, y: 0.0}
                        }
                    },
                    {forKey:'to_nick',
                        value:{
                            text: '李四',
                            size: '14px',
                            color: '#0000ff',
                            family: '宋体',
                            offset: {x: 0.0, y: 0.0}
                        }
                    },
                    {forKey:'channel_info',
                        value:{
                            text: '送送送送送送送',
                            size: '16px',
                            color: '#00ff00',
                            family: '宋体',
                            offset: {x: 0.0, y: 0.0}
                        }
                    }];
                this.imageList = [
                    {
                        imageUrl: faverBtn,
                        forKey: 'gift_icon'
                    }
                ];
                this.loadingText='加载中...';
            }
        }
    };
</script>
<style>
</style>



